<template>
    <el-dialog title="" :visible.sync="formDialog" top="20vh" width="900px" :append-to-body="true" :close-on-click-modal="false" :before-close="handleClose">
        <el-card>
            <el-form label-width="90px" ref="form">
                <el-form-item label="">
                    <el-input v-model="form.name" disabled size="small" maxlength="50" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.username" disabled size="small" maxlength="50" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.phone" disabled size="small" maxlength="50" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.inviter" disabled size="small" maxlength="50" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.inviterPhone" disabled size="small" maxlength="50" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.inviterCode" disabled size="small" maxlength="50" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.remarks" disabled type="textarea" :rows="3" placeholder="">
                    </el-input>
                </el-form-item>
            </el-form>
        </el-card>
        <el-card v-if="form.otherdata.length>0" style="margin-top:20px;">
            <div slot="header">
                <span></span>
            </div>
            <div>
                <el-table :data="form.otherdata" style="width: 100%">
                    <el-table-column prop="price" label="" width="150">
                    </el-table-column>
                    <el-table-column prop="createAt" label="" width="200">
                        <template slot-scope="scope">
                            {{formatDate(scope.row.createAt)}}
                        </template>
                    </el-table-column>
                    <el-table-column prop="remarks" label="">
                    </el-table-column>
                    <el-table-column label="" width="120">
                        <template slot-scope="scope">
                            <el-button size="small" icon="el-icon-delete" type="danger" @click="delete_handle(scope)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </el-card>
        <div slot="footer" class="dialog-footer">
            <el-button @click="handleClose"></el-button>
        </div>
    </el-dialog>
</template>

<script>
import { formatDate } from "@/components/lib/util";
export default {
  props: {
    id: {
      type: Number,
      default: 0
    },
    formDialog: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isUpdate: false,
      form: {
        otherdata: []
      }
    };
  },
  methods: {
    formatDate,
    handleClose() {
      this.$emit("dialogHandle", { dialog: false, isreload: this.isUpdate });
    },
    delete_handle(scope) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.http
            .delete("/api/cashback/" + scope.row.id, { params: { type: 0 } })
            .then(res => {
              if (res.code == 200) {
                this.$message.success("");
                this.form.otherdata.splice(scope.$index, 1);
              } else this.$message.error(res.msg);
            });
        })
        .catch(() => {});
    }
  },
  watch: {
    formDialog() {
      if (this.formDialog) {
        this.http.get("/api/crm/wcashbackitem/" + this.id).then(res => {
          this.form = res.data;
        });
      }
    }
  }
};
</script>

<style>
</style>



